<template>
  <el-dialog ref="dialog" v-model="dialogVisible" width="40%" >
    <el-container>
      <el-header class="el-menu__title" style="margin-bottom:2px;text-align: center" >
        <span>欢迎注册</span>
      </el-header>
      <el-main >
        <div>
          <el-row>
            <el-col style="text-align:center;margin-top:10px">
              <span>用户类别：</span>
              <el-select v-model="user.role" clearable @change="isShow"
                         size="small">
                <el-option
                    v-for="item in SystemRole"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="text-align:center;margin-top:10px">
              <span>&#12288;用户名：</span>
              <el-input v-model="user.username" size="small" style="width: 200px;"></el-input>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="text-align:center;margin-top:10px">
              <span>密&#12288;&#12288;码：</span>
              <el-input v-model="user.password" size="small" style="width: 200px;" show-password></el-input>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="text-align:center;margin-top:10px">
              <span>确认密码：</span>
              <el-input v-model="user.confirm" size="small" style="width: 200px;" show-password></el-input>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="text-align:center;margin-top:10px">
              <span>姓&#12288;&#12288;名：</span>
              <el-input v-model="user.nickName" size="small" style="width: 200px;"></el-input>
            </el-col>
          </el-row>
          <el-row v-show="isShowData">
            <el-col style="text-align:center;margin-top:10px">
              <span>性&#12288;别：</span>
              <el-select v-model="user.sex" clearable
                         size="small">
                <el-option
                    v-for="item in gender"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row v-show="isShowData">
            <el-col style="text-align:center;margin-top:10px">
              <span>年&#12288;&#12288;龄：</span>
              <el-input v-model="user.age" size="small" style="width: 200px;"></el-input>
            </el-col>
          </el-row>
          <el-row v-show="isShowData">
            <el-col style="text-align:center;margin-top:10px">
              <span>手机号码：</span>
              <el-input v-model="user.tel" size="small" style="width: 200px;"></el-input>
            </el-col>
          </el-row>
          <el-row v-show="isShowData">
            <el-col style="text-align:center;margin-top:10px">
              <span>家庭住址：</span>
              <el-input v-model="user.address" size="small" style="width: 200px;"></el-input>
            </el-col>
          </el-row>
        </div>
      </el-main>
      <el-footer style="text-align: center;">
        <el-button size="mini" type="primary" @click="register()">确定</el-button>
        <el-button size="mini" type="primary" @click="modifyBillDialogCancel()">取消</el-button>
      </el-footer>
    </el-container>
  </el-dialog>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "Register",
  data(){
    return{
      dialogVisible:false,
      user:{},
      isShowData:true,
      SystemRole:[
        {value:"0", label:"管理员"},
        {value:"1", label:"病人"},
        {value:"2", label:"专家"},
      ],
      gender:[
        {value:"男",label:"男"},
        {value:"女",label:"女"},
      ],
    }
  },

  methods:{
    init() {
      this.setDialogVisible(true);
    },
    setDialogVisible(val){
      this.dialogVisible = val
      this.user = {}
    },
    modifyBillDialogCancel(){
      this.setDialogVisible(false);
      this.user = {};
    },
    isShow(val){
      if(val === '0'){
        this.isShowData = false;
        this.user= {}
        this.user.role = val
      }else if(val === '1'){
        this.isShowData = true;
        this.user= {}
        this.user.role = val
      }else {
        this.isShowData = true;
        this.user= {}
        this.user.role = val
      }
    },
    commitData() {
      debugger
      let chinese = /^([\u4e00-\u9fa5]{2,5})$/;
      //密码强度正则，最少8位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
      let pPattern = /^.*(?=.{8,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z]).*$/;
      if (this.user.role == "" || this.user.role == undefined
          || this.user.role == null) {
        this.$alert('请选择用户类别', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.username == "" || this.user.username == undefined
          || this.user.username == null) {
        this.$alert('用户名不能为空', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.username.length <6 || this.user.username.length >20) {
        this.$alert('用户名的长度不能小于6或者大于20', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.password == "" || this.user.password == undefined
          || this.user.password == null) {
        this.$alert('密码不能为空', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (!pPattern.test(this.user.password)) {
        this.$alert('密码最少8位，包括至少1个大写字母，1个小写字母，1个数字', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.confirm == "" || this.user.confirm == undefined
          || this.user.confirm == null) {
        this.$alert('确认密码不能为空', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.password != this.user.confirm) {
        this.$alert('两次密码输入不一致，请重新输入', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (this.user.nickName == "" || this.user.nickName == undefined
          || this.user.nickName == null) {
        this.$alert('姓名不能为空', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      } else if (!chinese.test(this.user.nickName)) {
        this.$alert('姓名的长度不能小于2个汉字或者大于5个汉字', '提示', {
          confirmButtonText: '确定',
          type:"warning"
        });
        return false;
      }

      if(this.user.role === '1' || this.user.role === '2' ){
        let reg = /^1[3456789]\d{9}$/;
        if (this.user.sex == "" || this.user.sex == undefined
            || this.user.sex == null) {
          this.$alert('请选择性别', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        } else if (this.user.age == "" || this.user.age == undefined
            || this.user.age == null) {
          this.$alert('请填写您的年龄', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        } else if (this.user.age < 0 || this.user.age > 120) {
          this.$alert('请填写0到120岁的年龄', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        } else if (this.user.tel == "" || this.user.tel == undefined
            || this.user.tel == null) {
          this.$alert('请填写您的手机号码', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        } else if (!reg.test(this.user.tel)) {
          this.$alert('请填写正确的手机号码', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        } else if (this.user.address == "" || this.user.address == undefined
            || this.user.address == null) {
          this.$alert('请填写您的家庭住址', '提示', {
            confirmButtonText: '确定',
            type:"warning"
          });
          return false;
        }
      }
    },
    register(){
      let f = this.commitData();
      if(f == false){
        return
      }
      let url = ""
      if(this.user.role === '0'){
        //管理员登录
        url = "/root/register"
      }else if(this.user.role === '1'){
        //病人登录
        url = "/patient/register"
      }else{
        //专家登录
        url = "/specialist/register"
      }
      request.post(url,this.user).then(res => {
        if (res.code === '0') {
          this.$message({
            type: "success",
            message: "注册成功"
          })
          this.setDialogVisible(false);
        } else {
          this.$message({
            type: "error",
            message: res.msg
          })
        }
      })
    }
  }
}
</script>

<style scoped>
.el-menu__title{
  font-family: "Microsoft YaHei";
  font-size: 1.5rem;
  color: #0682C5;
  letter-spacing: 0;
  line-height: 33px;
}
.el-badge-class {
  color: #12A0EB;
  margin-top: 10px;
  margin-left: -30px;
}
.tips-class {
  font-family: PingFangSC-Medium;
  color: #ff0000;
  letter-spacing: 0;
  margin-top: -1.5%;
  font-size: 10px;
}
</style>
